import { Injectable } from '@angular/core';
import { IOmniCardItem } from '../../psComponents/ps-container-lookup-option/ps-option-card/ps-option-card.component.interface';
import { CommonBussinessConstant } from '../models/ps-common-bussiness-constant';
import { IOmniCardsRequest, IOmniCommonResponse } from '../models/ps-common-bussiness-interfaces';
import { OmniPullService } from '../omni-common/omni-pull.service';
/*
Created by Waleed Aly 03 April 2023 to encapsulated repeated code needed in different cards components "debit,credit,prepaid"
*/
@Injectable({
  providedIn: 'root'
})
export class CardsService {

  constructor(private OmniPull: OmniPullService) { }

  static readonly cardListEndPoint: string = "rest/omniCorePull/cardList";
  static readonly prepaidCardListEndPoint: string = "rest/omniCorePull/returnPrePaidCardsList";
  public static readonly CREDIT_CARD_TYPE_NAME = "CREDIT";
  public static readonly DEBIT_CARD_TYPE_NAME = "DEBIT";
  public static readonly PREPAID_CARD_TYPE_NAME = "PREPAID";

  public populateCards(Cards: Array<IOmniCardItem>, _cardTypeName: string): IOmniCardItem[] {
    let listOfCards: IOmniCardItem[] = [];
    for (const iterator of Cards) {
      if (iterator.cardNo) {
        const cardItem: IOmniCardItem = {
          cardNumber: iterator.cardNo,
          cardName: iterator.cardName,
          oldCardName: iterator.oldCardName,
          cardType: iterator.cardType,
          cardTypeName: _cardTypeName,
          //  cardTypeName: iterator.cardType === CommonBussinessConstant.DEBIT_CARD_TYPE ? CommonBussinessConstant.DEBIT_CARD_TYPE_NAME : CommonBussinessConstant.CREDIT_CARD_TYPE_NAME,
          posLimitAmount: iterator.posLimitAmount,
          withdrawalLimit: iterator.cardWdLimitAmount,
          status: iterator.status,
          statusCode: iterator.statusCode,
          limitCheckingBy: iterator.limitCheckingBy,
          currencyBriefName: iterator.currencyBriefName || iterator.currencyBriefNameEnglish,
          currencyBriefNameEnglish: iterator.currencyBriefNameEnglish,
          expiryDate: iterator.expiryDate,
          currencyCountryFlag: iterator.currencyCountryFlag,
          cardNumberMask: iterator.cardNumberMask,
          primaryAccount: iterator.primaryAccount, //added by salah primary account for AZDB-4575,
          cardLimit: iterator.cardLimit,
          remainingBalance: iterator.remainingBalance,
          settlementRate: iterator.settlementRate,
          debitOrCredit: (_cardTypeName == CardsService.CREDIT_CARD_TYPE_NAME || _cardTypeName == CardsService.DEBIT_CARD_TYPE_NAME) ? iterator.debitOrCredit : undefined,
          outstandingBalance: iterator.outstandingBalance,
          ownerName: iterator.cardHolderName,
          loyaltyPoint: iterator.loyaltyPoint,
          lookupKey: iterator.cardNo,
          applicationId: iterator.applicationId,
          additionalRef: iterator.additionalRef,
          accGl: iterator.accGl,
          branch: iterator.branch,
          cif: iterator.cif,
          currency: iterator.currency,
          serialNo: iterator.serialNo,
          previewProperty: 'cardName',
          primarySupplementary: iterator.primarySupplementary,
          title: iterator.cardName + " - " + ((_cardTypeName == CardsService.CREDIT_CARD_TYPE_NAME || CardsService.DEBIT_CARD_TYPE_NAME) ? (iterator.cardNumberMask || iterator.cardNo) : iterator.cardNo), //Modified by ghada, ABEOI230172 for showing the masked card number in card title as requested by ABE
          referenceCard: (_cardTypeName == CardsService.PREPAID_CARD_TYPE_NAME) ? iterator.referenceCard : undefined,
          issueDate: (_cardTypeName == CardsService.PREPAID_CARD_TYPE_NAME) ? iterator.issueDate : undefined,
          defaultAcc: iterator.defaultAcc,//added by ghada 23102023 ,AZDB-1137 for IDB card modifications
          accnumbs: iterator.accnumbs,
          cardCode: iterator.cardCode,
          cardRefnum: iterator.cardRefnum
        };
        listOfCards.push(cardItem);
      }
    }
    return listOfCards;
  }

  /*
    /*
          Params:
      IOmniCardsRequest
     Returns:
       IOmniCardItem
     Description:
       used to get cardList
   */
  returnCardList(parameter: IOmniCardsRequest): Promise<IOmniCommonResponse> {
    parameter.apiCode = -1;
    return new Promise<IOmniCommonResponse>((resolve, reject) => {
      this.OmniPull.commonRequestAjax(parameter.cardType === CommonBussinessConstant.CARD_SEGMENT_NAME_PREPAID
        ? CardsService.prepaidCardListEndPoint : CardsService.cardListEndPoint, parameter)//the prepaid part added by Marina 06 Dec 2022 for IDB IDB210116 
        .then((result) => {
          const list: Array<IOmniCardItem> = result.data
            .gridModel as Array<IOmniCardItem>;
          resolve({
            gridModel: list,
            totalNbRec: result.data.totalNbRec,
          });
        })
        .catch((error) => {
          reject(error);
        });
    });
  }

}
